.containers {
    display: flex;  
}

.main-div {
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 16px;
    margin: 16px;
    width: 200px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.name {
    color: #333;
    font-size: 18px;
    margin-bottom: 8px;
}

.price {
    color: #007bff;
    font-size: 16px;
    margin-bottom: 8px;
}

.description {
    color: #555;
    font-size: 14px;
    margin-bottom: 8px;
}

.category {
    color: #28a745;
    font-size: 14px;
    margin-bottom: 8px;
}

.stock {
    color: #dc3545;
    font-size: 14px;
    margin-bottom: 8px;
}

.Link{
    text-decoration: none;
}

@media (max-width: 768px) {
    .containers {
        flex-direction: column;
        align-items: center;
    }

    .main-div {
        max-width: 200px;
        transform: translate(-2rem);        
    }
}